/*** IMPORTS FROM imports-loader ***/
var define = false;

import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';

document.addEventListener('DOMContentLoaded', () => {
  const desktop = window.matchMedia('(min-width: 60em)');
  const isScrollActive = (boolean, target) => {
    if (!desktop.matches) {  
      if (boolean) {
        enableBodyScroll(target);
      } else {
        disableBodyScroll(target)
      }
    }
  };

  const btnShowModalForm = document.querySelector('#showModalForm');
  const btnCloseModalForm = document.querySelector('#modalFeedbackClose');
  const modalForm = document.querySelector('#modalFeedback');

  btnShowModalForm.addEventListener('click', () => {
    modalForm.style.transform = 'translateX(0)';
    isScrollActive(false, modalForm);
  });

  btnCloseModalForm.addEventListener('click', () => {
    modalForm.style.transform = null;
    isScrollActive(true, modalForm);
  });
})
