/*** IMPORTS FROM imports-loader ***/
var define = false;

import IMask from 'imask';
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';

document.addEventListener('DOMContentLoaded', () => {
  const desktop = window.matchMedia('(min-width: 60em)');
  const isScrollActive = (boolean, target) => {
    if (!desktop.matches) {  
      if (boolean) {
        enableBodyScroll(target);
      } else {
        disableBodyScroll(target)
      }
    }
  };

  const formFeedback = document.querySelector('#mainFeedbackForm');
  const formCall = document.querySelector('#formCall');
  const modalFeedback = document.querySelector('#modalFeedback');
  const modalForm = document.querySelector('#modalForm');
  const modalSuccess = document.querySelector('#modalOk');

  // inputMask
  const formFeedbackMask = new IMask(
    formFeedback.querySelector('.form-feedback_phone'), {
      mask: '+{7} (000) 000-00-00',
    },
  );

  const formCallMask = new IMask(
    formCall.querySelector('input[name=phone]'), {
      mask: '+{7} (000) 000-00-00',
    },
  );

  const modalFormMask = new IMask(
    modalForm.querySelector('.feedback-form_phone'),
    { mask: '+{7} (000) 000-00-00' },
  )

  // submit forms
  formFeedback.addEventListener('submit', (e) => {
    e.preventDefault();

    const name = e.currentTarget.querySelector('input[name=name]');
    const phone = e.currentTarget.querySelector('input[name=phone]');
    const message = e.currentTarget.querySelector('textarea[name=message]');
    const agreements = e.currentTarget.querySelector('#feedback-agreements');

    if (name.value && phone.value.length === 18 && message.value && agreements.checked) {
      const formData = new FormData(e.currentTarget);

      fetch('/cart/registration', {
        method: 'POST',
        mode: 'no-cors',
        body: formData,
      }).then((res) => {
        if (res.ok) {
          modalSuccess.style.transform = 'translateX(0)';
          setTimeout(() => { modalSuccess.style.transform = null; }, 2000);
        }
      }).catch((err) => {
        console.log(`! ${err}`);
      });
    }

    // remove for prod
    modalSuccess.style.transform = 'translateX(0)';
    setTimeout(() => { modalSuccess.style.transform = null; }, 2000);
  })

  formCall.addEventListener('submit', (e) => {
    e.preventDefault();

    const phone = e.currentTarget.querySelector('input[name=phone]');

    if (phone.value.length === 18) {
      const formData = new FormData(e.currentTarget);

      fetch('/cart/registration', {
        method: 'POST',
        mode: 'no-cors',
        body: formData,
      }).then((res) => {
        if (res.ok) {
          if (desktop.matches) {
            const success = document.querySelector('.hero_success');
            success.classList.add('hero_success--show');
            setTimeout(() => {
              success.classList.remove('hero_success--show');
            }, 1000);
          } else {
            alert('    !  ,      ;)');
          }
        }
      }).catch((err) => {
        console.log(`! ${err}`);
      });
    }

    // remove for prod
    if (desktop.matches) {
      const success = document.querySelector('.hero_success');
      success.classList.add('hero_success--show');
      setTimeout(() => {
        success.classList.remove('hero_success--show');
      }, 1000);
    } else {
      alert('    !  ,      ;)');
    }
  });

  modalForm.addEventListener('submit', (e) => {
    e.preventDefault();

    const modal = document.querySelector('#modalFeedback');

    const name = e.currentTarget.querySelector('input[name=name]');
    const phone = e.currentTarget.querySelector('input[name=phone]');
    const message = e.currentTarget.querySelector('textarea[name=message]');
    const agreements = e.currentTarget.querySelector('#modal-feedback-agreements');

    if (name.value && phone.value.length === 18 && message.value && agreements.checked) {
      const formData = new FormData(e.currentTarget);

      fetch('/cart/registration', {
        method: 'POST',
        mode: 'no-cors',
        body: formData,
      }).then((res) => {
        if (res.ok) {
          modal.style.transform = null;
          modalSuccess.style.transform = 'translateX(0)';
          isScrollActive(true, modalFeedback);
          setTimeout(() => { modalSuccess.style.transform = null; }, 2000);
        }
      }).catch((err) => {
        console.log(`! ${err}`);
      });
    }

    // remove for prod
    modal.style.transform = null;
    modalSuccess.style.transform = 'translateX(0)';
    isScrollActive(true, modalFeedback);
    setTimeout(() => { modalSuccess.style.transform = null; }, 2000);
  });
});


